<template>
  <section id="section1">

    <page1 v-show="pageNum === 1" @pageUp="pageUp"></page1>
    <page2 v-show="pageNum === 2" @pageDown="pageDown"></page2>

    <scroll @scroll="scroll('#section2')"></scroll>

  </section>
</template>

<script>
import scroll from '../../components/Scroll.vue'
import {sectionScroll} from '../../utils/scroll.js';
import page1 from './pages/Page1.vue'
import page2 from './pages/Page2.vue'

  export default {
    data: function () {
      return {
        pageNum: 1
      }
    },
    computed: {
      location () {
        return this.$store.state.scormLMS.location
      }
    },
    methods: {
      pageUp() {
        this.pageNum += 1
      },
      pageDown() {
        this.pageNum -= 1
      },
      scroll(section) {
        sectionScroll(section)
      }
    },
    mounted() {

    },
    components: {
      scroll, page1, page2
    },
    props: []
  }
</script>

<style scoped>

</style>



